.link {
	color: #00BC9B;
	font-size: 1rem;
	text-decoration: none;
}

@media screen and (min-width: 800px) {
	.link {
		display: block;
		color: #ffffff;
		font-size: 16px;
		width: 100%;
		height: 40px;
		line-height: 40px;
		border: none;
		background: #00BC9B;
	}
}


